@import 'fonts';
@import 'variables';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    padding: 0;
    margin: 0;
    background: #DBE5E7;
}

p {
    margin-bottom: 0;
}

header, main, footer {
    margin: auto;
    max-width: 1920px;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

.main-container {
    position: relative;
    background: $white-color;
    margin: auto;
    max-width: 1920px;
    overflow: hidden;
}

.container {
    width: 100%;
    margin: auto;
    padding: 0 $size-70px;
}

.button {
    padding: 0 14px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: clamp(18px, 2.34vw, 60px);
    font-family: "Golos Text", sans-serif;
    font-weight: 500;
    font-style: normal;
    border-radius: 17px;
    font-size: $size-14px;

    &_middle {
        height: clamp(30px, 4.16vw, 90px);
    }

    &_big {
        height: clamp(30px, 5.97vw, 114px);
        font-size: clamp(10px, 1.94vw, 40px);
        padding-left: clamp(10px, 4.72vw, 90px);
        padding-right: clamp(10px, 4.72vw, 90px);
        border-radius: clamp(20px, 3vw, 60px);
    }
}

.border-gray {
    border: 1px solid $grey-border-color;
    color: $black-color;
}

.dark-bg {
    background: $dark-background;
    color: $white-color;
}

.light-blue-bg {
    background: $light-blue-background;
    color: $white-color;
    text-transform: uppercase;
}



//whatsapp-link
.whatsapp {
    font-size: $size-15px;

    &::before {
        padding-right: 5px;
        color: #25D366;
    }
}

//main title block
.title-block {
    margin: auto;
    padding-top: 50px;
    padding-bottom: 20px;
    text-align: center;

    h1 {
        margin: auto;
        margin-bottom: 30px;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: clamp(20px, 3.47vw, 65px);
        line-height: 1.05;
    }
    
    p {
        margin: auto;
        margin-bottom: 30px;
        max-width: clamp(250px, 51.94vw, 1000px);
    }
}

.text {
    font-family: 'Golos Text', sans-serif;
    font-weight: 400;
    font-size: clamp(8px, 1.24vw, 23px);
    line-height: 1.3;
    color: $black-color;
}

//feedback block
.feedback-block {
    margin-bottom: $size-70px;
    text-align: center;
    &__link {
        margin-left: 20px;
        color: $black-color;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: $size-14px;
        line-height: 1.2;
        text-transform: uppercase;
    }
}

.banner {
    margin-bottom: 70px;
    width: 100%;

    &__img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

}

.key-word {
    display: inline-block;
    padding: 6px 14px;
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: $size-14px;
    line-height: 1.2;
    text-transform: uppercase;
    border-radius: 17px;
    border: 1px solid $grey-border-color;
}

.second-title {
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: clamp(16px, 2.22vw, 41px);
    line-height: 1.16;
    color: $black-color;
}

.section {
    margin-bottom: $size-70px;
}

.our-business {
    text-align: center;  

    &__key-word {
        margin-bottom: 30px;
        color: $grey-key-word-color;
    }

    .second-title {
        max-width: clamp(320px, 64vw, 1186px);
        margin: auto;
        margin-bottom: $size-50px;
    }

    //big cards
    &__big-cards {
        margin-bottom: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }

    &__big-card {
        max-width: 848px;
        padding: $size-30px;
        flex-grow: 1;
        height: 100px;
        background: $dark-background;
        border-radius: 30px;
        height: clamp(100px, 20.8vw, 400px);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom;
        color: $light-gray-color;
        font-family: 'Golos Text', sans-serif;
        font-weight: 400;
        line-height: 1.1;
        text-align: left;
        cursor: pointer;
        transition: all .2s ease-in-out;

        &:first-child {
            background-image: url('../img/big-card-bg-image-1.svg');
        }
        &:last-child {
            background-image: url('../img/big-card-bg-image-2.svg');
        }

        .text {
            color: $white-color;
        }

        &:hover {
            background-color: #0b4655;
        }


    }

    &__small-cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: $size-20px;
        grid-auto-rows: clamp(100px, 14vw, 500px);
    }

    &__small-card {
        padding: $size-30px;
        border-radius: 30px;
        border: 1px solid $grey-border-color;
        text-align: left;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: all .2s ease-in-out;
        cursor: pointer;
        box-sizing: border-box;

        .text {
            line-height: 1.1;
        }

        i {
            font-size: $size-40px;
            line-height: 0.8;
        }

        &:nth-child(1) {
            .text {
                width: clamp(50px, 15.4vw, 290px);
            }
        }
        &:nth-child(2) {
            .text {
                width: clamp(50px, 15.4vw, 295px);
            }
        }
        &:nth-child(4) {
            .text {
                width: clamp(50px, 14.4vw, 220px);
            }
        }
        &:nth-child(5) {
            .text {
                width: clamp(50px, 15.4vw, 295px);
            }
        }

        &:hover {
            background: $dark-background;
            color: $white-color;
            
            .text {
                color: $white-color;
            }
        }
    }

    .text {
        margin-bottom: clamp(2px, 0.8vw, 20px);
    }

    &__small-text {
        font-size: clamp(8px, 0.9vw, 17px);
        line-height: 1.2;
    }
}

.feedback {
   &__wrapper {
    padding: clamp(20px, 3.46vw, 60px);
    display: flex;
    background: #EFF4F5;
    border-radius: 30px;
    justify-content: space-between;
    align-items: center;
    
    .second-title {
        width: clamp(200px, 34vw, 800px);

        &_string {
            display: block;
        }
    }
   }
}

.form {
    width: clamp(200px, 40.6vw, 800px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    font-size: 18px;

    &__input {
        padding-left: 20px;
        box-sizing: border-box;
        flex-grow: 1;
        height: clamp(40px, 4.16vw, 80px);
        border-radius: 20px;
        border: none;
        background: $white-color;

        &::placeholder {
            color: #98A8AE;
        }
        
    }

    &__button {
        width: 100%;
        border-radius: 20px;
        height: clamp(40px, 4.16vw, 80px);
        font-weight: 500;
    }

    .dark-bg {
        color: $white-color;
    }
   
}

.geo {
    &__map {
        display: inline-block;
        width: clamp(320px, 51.6vw, 1200px);
        flex-shrink: 0;
    }

    &__img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

    &__text-content {
        display: inline-flex;
        flex-direction: column;
        gap: $size-20px; 
    }

    .button {
        margin-top: clamp(10px, 2.2vw, 90px);
        width: min-content;
        text-transform: uppercase;
    }

    &__wrapper {
        display: flex;
        gap: $size-50px;
    }

    .feedback-block {
        text-align: left;
    }
}


.faq {

    &__list {
        padding-top: clamp(5px, 0.8vw, 20px);
        list-style: none;
        width: clamp(320px, 59vw, 1130px);
    }
    
    &__item {
        padding-bottom: $size-20px;
        padding-top: $size-20px;
        position: relative;
        transition: all .2s linear; 
        
        &:not(:last-child) {
            border-bottom: 1px solid $grey-border-color;
        }
    }

    &__question {
        position: relative;

        &._active {
            margin-bottom: 10px;
        }

        &::after {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            width: clamp(10px, 2.08vw, 45px);
            height: clamp(10px, 2.08vw, 45px);
            background-image: url("../img/cross-icon.svg");
            background-size: contain;
            background-position: center;
            transform: rotate(45deg);
            transition: transform .2s ease-in;  
        }
    }
    
    &__question-text {
        font-family: "Golos Text", sans-serif;
        font-size: clamp(5px, 1.66vw, 30px);
        line-height: 1.1;
        color: $black-color;
        cursor: pointer;
    }

    &__text {
        opacity: 0;
        transition: all .2s linear;
        max-height: 0;
    }

    ._active::after {       
        transform: rotate(90deg);
        transition: transform .2s ease-in;
    }
}

//footer
.footer {
    padding: clamp(3px, 2.4vw, 40px) 0;
    background: $footer-bg-color;

    &__text {
        font-family: "Golos Text", sans-serif;
        font-weight: 400;
        line-height: 1.3;
        font-size: $size-15px;
        color: $black-color; 
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
       
        grid-template-areas: 
        "menu service contacts"
        "rights service design";
    }

    &__menu {
        grid-area: menu;
    }

    &__service {
        grid-area: service;
    }

    &__contacts {
        grid-area: contacts;

    }

    &__rights {
        grid-area: rights;
    
        align-self: end;
        color: $footer-gray-text;
    }

    &__design {
        grid-area: design;

        text-align: end;
        align-self: end;

        span {
            display: block;
        }

        &_title {
            color: $footer-gray-text;
        }

        &_link {
            text-decoration: underline;
            line-height: 1.4;
        }
    }

    &__design-block {
        display: inline-block;
        text-align: left;
    }

    &__service-blocks {
        position: relative;
        display: flex;
    }

    &__service-block {
        width: 49%;
    }

    &__service-title {
        margin-bottom: clamp(3px, 0.9vw, 18px);
        color: $footer-gray-text;
        line-height: 1.3;
    }

    &__service-item {
        margin-bottom: clamp(3px, 0.9vw, 18px);
        display: inline-block;
        max-width: clamp(80px, 18.2vw, 360px);
        line-height: 1.3;
    }

    &__contacts {
        margin-top: clamp(15px, 2vw, 50px);
        text-align: end;
    }

    .phone-number {
        display: block;
        margin-bottom: 10px;
        font-family: "Golos Text", sans-serif;
        color: $black-color;
        text-decoration: underline;
        font-size: clamp(5px, 1.25vw, 28px);
        line-height: 1.5;
    }

    

    .menu__link {
        display: inline-block;
        margin-bottom: clamp(3px, 0.9vw, 18px);
        line-height: 1.3;
    }
}

@import 'media';
@import 'blocks/header/header';
@import 'blocks/header/media';